@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
a {
  text-decoration: none;
  color: inherit;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header{width:100%;cursor:pointer;position:fixed;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;text-transform:uppercase;font-family:"Oswald", sans-serif;font-size:4vw;font-weight:bold;padding-bottom:10px;z-index:100;box-shadow:0px 2px 12px #ea2027}.header__dark{background-color:#000;color:#f0f0f0}.header__light{background-color:#f0f0f0;color:#000}@media (max-width: 1000px){.header{padding-top:10px;font-size:6.4vw}}

.carouselItem{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;object-fit:contain;padding:10px}.carouselItem__img{border-radius:10px;height:150px;width:100px;margin-bottom:5px;box-shadow:0px 0px 5px black}

.ContentModal__landscape{object-fit:contain;border-radius:10px}.ContentModal__portrait{display:none;object-fit:contain;border-radius:10px}.tagline{padding-bottom:10px;-webkit-align-self:center;align-self:center}.ContentModal{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;height:100%;width:100%;overflow-y:scroll;scrollbar-width:none}.ContentModal::-webkit-scrollbar{display:none}.ContentModal__about{padding:10px;width:95%;height:90%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;font-family:"Open Sans", sans-serif;-webkit-justify-content:space-evenly;justify-content:space-evenly}.ContentModal__title{height:12%;font-size:4vw;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.ContentModal__description{display:-webkit-flex;display:flex;height:40%;overflow-y:scroll;padding:15px;border-radius:20px;scrollbar-width:thin;box-shadow:inset 0 0 5px #000000;text-align:justify}.ContentModal__description::-webkit-scrollbar{display:none}@media (min-width: 835px){.ContentModal__landscape{display:none}.ContentModal__portrait{display:-webkit-flex;display:flex;width:38%}.ContentModal{-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-around;justify-content:space-around;padding:10px 0}.ContentModal__about{width:58%;padding:0;height:100%}.ContentModal__title{font-size:2.5vw}.ContentModal__description{font-size:22px}}

.media{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:200px;padding:5px;margin:5px 0;border-radius:10px;position:relative;font-family:"Montserrat", sans-serif}@media (max-width: 550px){.media{width:46%}}.poster{border-radius:10px}.poster:hover{-webkit-filter:sepia(1);filter:sepia(1);transition:500ms all}.title{width:100%;text-align:center;font-size:17px;padding:8px 0}.subTitle{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;padding-bottom:3px;padding:0 2px;padding-bottom:3px}

.trending{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-around;justify-content:space-around}

.search{display:-webkit-flex;display:flex;margin:15px 0}

*{scroll-behavior:smooth}.app{min-height:100vh;padding-top:130px;padding-bottom:70px}.app__dark{background-color:#000}.app__light{background-color:#f0f0f0}@media (max-width: 700px){.app{padding-top:70px}}.pageTitle{text-transform:uppercase;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;font-family:"Montserrat", sans-serif;font-size:2vw;font-weight:bold;padding:4px;border-radius:50px}.pageTitle__dark{color:#f0f0f0}.pageTitle__light{color:#000}@media (max-width: 1000px){.pageTitle{font-size:6.4vw}}

